<template>
  <div class="card">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Fleet Maintenance Schedule</span> &nbsp;
        </b-col>
        <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'View All'"
              classButton="button button--back"
              classIcon="icon-style-1"
              :disabled="false"
              @click="doViewAll"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body" style="height: 230px;">
      <div>
        <ACCFormList
          :prop="propList"
          :title="''"
          @rowClicked="rowClicked"
          ref="ref_list"
          @onRenderData="onRenderData"
        >
          <template slot="last_millage" slot-scope="data">
            <span>{{isCurrency(data.item.last_millage, 0)}}</span>
          </template>
          <template slot="current_millage" slot-scope="data">
            <span>{{isCurrency(data.item.current_millage, 0)}}</span>
          </template>
          <template slot="usage" slot-scope="data">
            <span>{{isCurrency(data.item.usage, 0)}}</span>
          </template>
        </ACCFormList>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propList: {
        OptionUrl: "/MM/MM_FleetMaintSch",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
        OrderBy: "",
        SourceField: "",
        ParamView: "",
        PerPage: 8,
      },
    };
  },
  computed: {},
  methods: {
    doViewAll() {
      this.$router.push({ name: "MM_FleetMaintenanceSchedule" });
    },
    rowClicked(record, index) {
      var param = record;
      param.isEdit = true;
      param.isView = true;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "MM_FleetMaintenanceScheduleForm" });
    },

    RenderData() {
      this.$refs.ref_list.doGetList("");
    },
  },
  mounted() {
    this.RenderData();
  },
};
</script>